.aboutSection {
    padding: 15px;
}

.aboutSection>h2 {
    font-size: 45px;
    font-weight: 400;
    text-align: center;
    text-decoration: underline;
    text-decoration-style: solid;
    margin-bottom: 7vh;
}

.childAboutSection {
    display: grid;
    grid-template-columns: 40% 60%;

}

.childAboutSection>:first-child {
    display: grid;
    justify-content: center;
}

.childAboutSection>:first-child>img {
    width: 300px;
    border: none;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.childAboutSection>:first-child+div {
    line-height: 36px;

}

.childAboutSection>:first-child+div>h3 {
    font-size: 22px;
    font-weight: 400;
    text-align: justify;
    /* border: 1px solid white; */
    width: 90%;
    letter-spacing: 2px;

}

/* media query */

/* tablet view */
@media screen and (min-width:426.95px) and (max-width:768px) {
  
    .childAboutSection {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .childAboutSection>:first-child+div {
        width: 90%;
    }

    .childAboutSection>:first-child+div>h3 {
        text-align: center;
        margin: 5%;
    }

}

/* mobile view */
@media screen and (min-width:280px) and (max-width:426.9px) {
    .aboutSection>h2 {
        font-size: 35px
    }
    .childAboutSection {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .childAboutSection>:first-child>img {
        width: 200px;
    }

    .childAboutSection>:first-child+div {
        width: 90%;
    }

    .childAboutSection>:first-child+div>h3 {
        font-size: 20px;
        text-align:justify;
        margin: 5%;
        letter-spacing: 2px;
    }

}